import form from "_platform/dsf/mixins/form.js"

let oneThingForm = {
  mixins: [form],
  provide() {
    return {
      $form: this
    }
  },
  data() {
    return {
      dynamicForms: []
    }
  },
  methods: {
    addFiled(ctrl) {
      if (!ctrl.isOneToMany) {
        if (ctrl.isFormControl) {
          this.postFiels.push(ctrl);
        } else if (ctrl.isDynamicForm) {
          this.dynamicForms.push(ctrl);
        }
      } else {
        this.subTables.push(ctrl);
      }
      this.updateMetaDataDict(ctrl);
    },
    clearDynamicFormInvalidKey() {
      _.each(this.dynamicForms, (dy) => {
        let prefix = dy.$prefix;
        let components = dy.components;
        let currentKeys = [];
        _.each(components, (comp) => {
          if (comp.isFormControl && comp.metadata_fullcode) {
            currentKeys.push(comp.metadata_fullcode)
          }
        })
        let keys = _.filter(Object.keys(this.$viewData), (key) => key.startsWith(prefix));
        _.each(keys, (key) => {
          if (currentKeys.indexOf(key) < 0) {
            this.$delete(this.$viewData, key)
          }
        });
      });
    },
    postKeysHandler() {
      this.clearDynamicFormInvalidKey();
    }
  }
}

//开放全局变量，二次开发时无法使用import时可以使用该全局变量混入
if (window) {
  window.$$form = oneThingForm;
}
export default oneThingForm;