import { cornflowerblue } from "color-name";

let form = {
  provide() {
    return {
      $form: this
    }
  },
  props: {
    nameSpace: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    params: {
      type: Object,
      default () {
        return {};
      }
    }
  },
  data() {
    return {
      validateRules: {},
      postFiels: [],
      subTables: [],
      loading: false,
      isView: false,
      ignoreValidate: false,
      notAllowPostKeys: []
    }
  },
  computed: {
    $errors() {
      let errors = [];
      for (let i = 0; i < this.postFiels.length; i++) {
        let field = this.postFiels[i];
        field.errors.length > 0 && errors.push(...field.errors);
      }
      return errors;
    },
    $dataKey() {
      let params = this.queryString;
      return params ? params.id : "";
    }
  },
  created() {
    this.$on("form.validateField", (evt) => {
      this.validateField(evt.value, evt.target)
    });
  },
  dataLoaded(data, next) {
    next();
  },
  methods: {
    validate() {
      return new Promise((resolve, reject) => {
        var fields = this.postFiels;
        let arrs = [];
        for (let i = 0; i < fields.length; i++) {
          let field = fields[i];
          if (!field.isValidate) {
            continue;
          }
          let fn = getValidateFunction.call(this, field.value, field);
          if (fn) {
            arrs.push(fn(field));
          }
        }
        Promise.all(arrs).then((result) => {
          let errors = [];
          _.each(result, (error) => {
            if (error && error instanceof ValidateError) {
              error.target.errors = [error];
              errors.push(error);
            }
          });
          !errors.length ? resolve() : reject(errors)
        });
      });
    },
    //调用数据加载接口
    $loadData() {
      if (this.$dataKey) {
        let params = {
          pageName: this.pageName,
          namespace: this.nameSpace,
          data: JSON.stringify({
            _id: this.$dataKey
          })
        };
        return new Promise((resolve, reject) => {
          // resolve({ data: {} })
          let url = "/meta/data?" + dsf.url.stringify(this.queryString)
          dsf.http.get(url, params)
            .done(data => {
              resolve(data);
            })
            .error(err => {
              reject({ message: err.message });
            });
        });
      }
    },
    //验证单个字段
    validateField(value, field) {
      if (field.isValidate) {
        let fn = getValidateFunction.call(this, value, field);
        if (fn) {
          let result = fn(field);
          if (result && result.then && result.catch) {
            result.then(() => {
              field.errors = [];
            }).catch((error) => {
              if (error) {
                // this._data.$errors.push(error);
                field.errors = [error];
              }
            });
          } else {
            if (result && result instanceof ValidateError) {
              field.errors = [result];
            } else {
              field.errors = [];
            }
          }
        }
      } else {
        field.errors = [];
      }
    },
    resetValidate() {
      var fields = this.postFiels;
      _.each(fields, (field) => {
        field.errors = [];
      });
    },
    addFiled(ctrl) {
      if (!ctrl.isOneToMany) {
        if (ctrl.isFormControl) {
          this.postFiels.push(ctrl);
        }
      } else {
        this.subTables.push(ctrl);
      }
      this.updateMetaDataDict(ctrl);
    },

    //更新metadataDict，因为页面中有存在动态创建的组件（子表，动态布局）
    updateMetaDataDict(ctrl) {
      if (this.metadataDict) {
        let fullCode = ctrl.metadata_fullcode;
        let metadata = ctrl.metadata;
        if (!metadata) {
          return {};
        }
        let arr = fullCode.split(".");
        let prefix = arr[0];
        let name = arr[1] ? arr[1] : "";
        if (metadata.level == 1 && !name) {
          this.metadataDict[fullCode] = { type: 'subtable', fieldsLength: 0, valueAttributes: [], level: 0 };
        } else {
          this.metadataDict[fullCode] = {
            type: 'field',
            fieldsLength: metadata.valueAttributes && metadata.valueAttributes.length,
            level: 0,
            valueAttributes: _.map(metadata.valueAttributes, 'code')
          };
          if (this.metadataDict[prefix]) {
            this.metadataDict[fullCode].level = 1;
            this.metadataDict[fullCode].inSubTable = prefix;
          }
        }
      }
    },
    removeFiled(ctrl) {
      if (ctrl && !ctrl.isOneToMany) {
        if (ctrl.isFormControl) {
          dsf.array.remove(this.postFiels, ctrl);
        } else if (ctrl.isDynamicForm) {
          dsf.array.remove(this.dynamicForms, ctrl);
        }
      } else if (ctrl && ctrl.isOneToMany) {
        dsf.array.remove(this.subTables, ctrl);
      }
    },
    postBefore() {
      let arr = [];
      _.each(this.postFiels, (field) => {
        let fn = field.postBefore;
        if (fn) {
          arr.push(fn());
        }
      });
      return Promise.all(arr);
    },
    //提交前数据键处理
    postKeysHandler(data) {
      let notPostKeys = [];
      for (let i = 0; i < this.postFiels.length; i++) {
        let ctrl = this.postFiels[i];
        if (!ctrl.allowPost) {
          notPostKeys.push(ctrl.metadata_fullcode);
        }
      }
      _.each(notPostKeys, (key) => {
        delete data[key];
      });
      for (let i = 0; i < notPostKeys.length; i++) {
        delete data[notPostKeys[i]];
      }
      _.each(this.subTables, (sub) => {
        let sbkey = sub.metadata_fullcode;
        if (data[sbkey]) {
          _.each(data[sbkey], (row) => {
            for (let k in row) {
              if (notPostKeys.indexOf(k) >= 0) {
                delete row[k];
              }
            }
          });
        }
      });
    },
    async formSave(options) {
      let opt = {
        isRefresh: dsf.isDef(options.isRefresh) ? options.isRefresh : true,
        savedClose: dsf.isDef(options.savedClose) ? options.savedClose : false,
        successMsg: options.successMsg || "保存成功",
        errorMsg: options.errorMsg || "保存失败",
        isSilent: options.isSilent || false
      }
      let saveResult = null;
      this.loading = true;
      //表单验证前触发
      let validateBefore = this.$options.validateBefore;
      //表单验证后触发
      let validateAfter = this.$options.validateAfter;
      let saveBefore = this.$options.saveBefore;
      let saveAfter = this.$options.saveAfter;
      let saveComplete = this.$options.saveComplete;
      try {
        // 判断是否问卷提交
        if (this.page && this.page.pageType === 'question') {
          // 问卷验证与数据处理
        } else {
          this.resetValidate();

        }
        for (let i = 0; i < this.subTables.length > 0; i++) {
          this.subTables[i].editAllEnd();
        }
        //轮询执行所有组件提交前事件
        await this.postBefore();
        if (validateBefore) {
          await promiseFn.call(this, validateBefore, [options]);
        }
        if (!this.ignoreValidate) {
          //执行验证函数
          await this.validate();
          if (validateAfter) {
            await promiseFn.call(this, validateAfter, [options]);
          }
        }
        if (saveBefore) {
          //执行表单保存前钩子函数
          await promiseFn.call(this, saveBefore, [options]);
        }
        //表单保存
        let result = await save.call(this, options);
        //更新表单数据
        saveResult = this.context.$$viewData = result;
        let params = dsf.mix({}, this.$route.query, { "id": result._id });
        //更新url添加id参数
        this.$router.replace({
          "path": this.$route.path,
          "query": params
        });
        if (saveAfter) {
          //执行表单保存后钩子函数
          await promiseFn.call(this, saveAfter, [saveResult, options]);
        }
        if (!opt.isSilent) {
          dsf.layer.message(opt.successMsg, true);
        }
        if (opt.isSilent) {
          return result;
        }
      } catch (err) {
        //抛出错误代码，有state表示后端返回的错误，20001的话提示报错错误,其他的显示后端返回的错误代码
        if (dsf.hasOwn(err, 'state')) {
          let message = err.state === 20001 ? options.errorMsg : err.message;
          dsf.layer.message(message, false);
        }
        //如果错误直接是文本，直接提示错误 
        else if (dsf.type(err) === "string") {
          dsf.layer.message(err, false);
        }
        //程序遇到的异常(error) 
        else if (dsf.type(err) === "error") {
          dsf.layer.message(opt.errorMsg, false);
          dsf.error(err);
        }
        // 验证错误信息，滚动到第一个错误
        if (dsf.type(err) === "array") {
          if (validateAfter) {
            await promiseFn.call(this, validateAfter, [options]);
          }
          err[0].target.$el.scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "nearest"
          });
        }
        saveResult = null;
      } finally {
        this.ignoreValidate = false;
        if (this.$openerVm) {
          //更新父页面数据
          this.$openerVm && this.$openerVm.reloadData && this.$openerVm.reloadData();
        }
        if (saveComplete) {
          //执行表单保存完成（无论成功失败）的钩子函数
          saveComplete.call(this, saveResult);
        }
        this.loading = false;
      }
    }
  }
}


function save() {
  return new Promise((resolve, reject) => {
    let _this = this;
    let data = dsf.mix(true, {}, this.$viewData);
    //处理子表数据
    _.each(this.subTables, (table) => {
      let key = table.metadata_fullcode;
      let tableData = data[key] || [];
      if (table.deleted) {
        let deletedRows = [];
        _.each(table.deleted, (delRow) => {
          if (delRow['_id']) {
            let row = _.find(tableData, (row) => row['_id'] == delRow['_id']);
            if (!row) {
              delRow[key + '.ds_deleted'] = 1;
              deletedRows.push(delRow);
            }
          }
        });
        data[key] = tableData.slice(0).concat(deletedRows);
      }
      _.each(tableData, (row, i) => {
        if (row[key + ".ds_deleted"] != 1) {
          row[key + ".ds_deleted"] = 0;
        }
        row[key + ".ds_order"] = i;
      });
    });
    this.postKeysHandler(data);
    let params = {
      namespace: this.nameSpace,
      pageName: this.pageName,
      data: data
    }
    if (this.$dataKey) {
      params.data['_id'] = this.$dataKey;
    }

    let saveHandler = this.$options.saveParamsHandler
    if (saveHandler) {
      saveHandler.call(this, params);
    }
    params.data = JSON.stringify(params.data, function(key, val) {
      // $开头的数据为前端产生的结构，不提交服务器否则会导致服务端
      if (!key.startsWith("$")) {
        return val;
      }
      return undefined;
    });
    this.$webAPI.formSave(params)
      .done((result) => {
        if (result.state == 20000) {
          _.each(this.subTables, (table) => {
            table.deleted = [];
          })
          resolve(result.data);
        } else {
          reject(result);
        }
      })
      .error((result) => {
        reject(result)
      })
  });
}

//获取组件的验证方法
function getValidateFunction(value, field) {
  let fn = null;
  //优先读取二次开发附加的validateRule
  if (field.validateRule) {
    fn = ruleProxy.call(this, this.validateRules[field.validateRule]).bind(this, value, field);
  }
  //读取组件是否自带内置验证函数
  else if (field.builtInValidate) {
    fn = ruleProxy.call(this, field.builtInValidate).bind(field, value, field);
  }
  //获取元数据验证格式 
  else if (field.metadata && field.metadata.validate) {
    fn = metadataValidateConfigValidator.bind(field, value, field);
  }
  return fn;
}

//错误类
function ValidateError(message, target, other) {
  this.message = message;
  this.target = target;
  if (other) {
    this.info = {
      ...other
    }
  } else {
    this.info = {}
  }
}

//代理执行validateRules中的规则
function ruleProxy(fn) {
  let proxyFn = fn.bind(this);
  if (proxyFn) {
    return function(value, field) {
      let result = proxyFn.call(this, value, field);
      if (dsf.isUnDef(result)) {
        return;
      }
      //如果函数返回一个Promise对象
      if (result.then && result.catch) {
        return Promise((resovle, reject) => {
          result.then(() => {
            resovle();
          })
          result.catch((valiResult) => {
            resovle(new ValidateError(valiResult.message, field, valiResult));
          });
        });
      } else {
        if (result.message) {
          return new ValidateError(result.message, field, result);
        }
      }
    }
  }
  return dsf.noop;
}

//元数据配置验证器
function metadataValidateConfigValidator(value, ctrl) {
  if (ctrl.metadata && ctrl.metadata.validate && ctrl.metadata.validate.config) {
    let valueAttributes = ctrl.metadata.valueAttributes || [];
    let validateConfig = ctrl.metadata.validate.config;
    for (let i = 0; i < valueAttributes.length; i++) {
      let va = valueAttributes[i];
      let valiConfig = validateConfig[va.code];
      if (valiConfig) {
        let v = null;
        if (valueAttributes.length == 1) {
          v = ctrl.value;
        } else {
          //有可能会是数组，比如下拉框多选，多选组件
          if (dsf.type(ctrl.value) == 'array') {
            let arr = ctrl.value;
            v = _.map(arr, va.code).join(",");
          } else {
            v = ctrl.value ? ctrl.value[va.code] : '';
          }
        }
        // let v = valueAttributes.length == 1 ? ctrl.value : (ctrl.value ? ctrl.value[va.code] : '');
        //验证是否必填
        if (valiConfig.required) {
          if (dsf.validate('isNull', v) === true) {
            return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.required, valiConfig), ctrl, { 'attributes': va.code }));
          }
        }
        if (v != '' && dsf.isDef(v)) {
          if (valiConfig.isNumber) {
            //验证是否是一个数字
            if (dsf.validate('isNumber', v) === false) {
              return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.isNumber, valiConfig), ctrl, { 'attributes': va.code }));
            }
            //验证数字最大值和最小值
            if (valiConfig.max) {
              if (dsf.validate('isNumberOver', v, valiConfig.max) === true) {
                return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.max, valiConfig), ctrl, { 'attributes': va.code }));
              }
            }
            if (valiConfig.min) {
              if (dsf.validate('isNumberUnder', v, valiConfig.min) === true) {
                return (
                  new ValidateError(
                    dsf.express.replace(valiConfig.errorMsg.min, valiConfig),
                    ctrl, { 'attributes': va.code }
                  ));
              }
            }
            //验证数字精度
            if (valiConfig.precision) {
              if (dsf.validate('precision', v, valiConfig.precision) === false) {
                return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.precision, valiConfig), ctrl, { 'attributes': va.code }));
              }
            }
          } else {
            //验证字符串是否超出长度
            if (valiConfig.max) {
              if (dsf.validate('maxLength', v, valiConfig.max) === true) {
                return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.max, valiConfig), ctrl, { 'attributes': va.code }));
              }
            }
            if (valiConfig.min) {
              if (dsf.validate('minLength', v, valiConfig.min) === true) {
                return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.min, valiConfig), ctrl, { 'attributes': va.code }));
              }
            }
          }
          //验证格式
          if (valiConfig.express) {
            if (dsf.validate(valiConfig.express, v) == false) {
              return (new ValidateError(dsf.express.replace(valiConfig.errorMsg.express, valiConfig), ctrl, { 'attributes': va.code }));
            }
          }
        }
      }
    }
  }
}

function promiseFn(callback, params) {
  return new Promise((resolve, reject) => {
    if (callback) {
      let next = (mark) => {
        if (mark === true || dsf.isUnDef(mark)) {
          resolve();
        } else {
          reject(mark);
        }
      }
      params = params || [];
      if (callback) {
        let fnString = callback.toString()
        let s = fnString.indexOf("{");
        let e = fnString.lastIndexOf("}");
        fnString = fnString.substring(s + 1, e);
        if (fnString.trim().indexOf("next(")<0) {
          callback && callback.apply(this, [next, ...params]);
          resolve();
        } else {
          callback && callback.apply(this, [next, ...params]);
        }
      }
    } else {
      resolve();
    }
  });
}



//开放全局变量，二次开发时无法使用import时可以使用该全局变量混入
if (window) {
  window.$$form = form;
  window.$$ValidateError = ValidateError
}
export default form;