/**
 * 全局过滤器
 * 文档：<a href="https://cn.vuejs.org/v2/guide/filters.html">文档</a>
 */

let r_allow = /\^/g;

let filters = {
  'proxy': function(v) {
    if (v) {
      let args = Array.prototype.slice.call(arguments, 0);
      if (args[1]) {
        let arr = args[1].split("^");
        if (filters[arr[0]]) {
          return filters[arr[0]].apply(this, [v, ...arr.slice(1)]);
        } else {
          return v;
        }
      } else {
        return v;
      }
    } else {
      return ""
    }
  },
  'proxyTitle': function(v) {
    if (v) {
      let args = Array.prototype.slice.call(arguments, 0);
      if (args[1]) {
        let arr = args[1].split("^");
        if (filters[arr[0]]) {
          if (arr[0].startsWith('html')) {
            return ""
          }
          let format = filters[arr[0]].apply(this, [v, ...arr.slice(1)]);
          return format;
        } else {
          return v;
        }
      } else {
        return v;
      }
    } else {
      return ""
    }
  },
  'htmlImage': function(v) {
    return v;
  },
  'dateTime': function(v, format = 'yyyy-mm-dd hh:ii', language, type) {
    if (v) {
      return dsf.date.format(v, format, language, type)
    }
    return '';
  },
  'timeAgo': function(date, accurate = true) {
    if (date) {
      return dsf.date.timeAgo(date, accurate)
    }
    return '';
  },
  'emptyFilter': function(v, text = '') {
    if (typeof v === 'function' ||
      dsf.type(v) === 'date') return v;

    if (dsf.isUnDef(v) ||
      isNaN(v) ||
      v.length === 0 ||
      dsf.isEmptyObject(v)) {
      return text;
    }
    return v;
  },
  'textValue': function(v, textOrValue) {
    if (v) {
      if (dsf.type(v) == "object") {
        return v[textOrValue]
      } else if (dsf.type(v) == "array") {
        return _.map(v, textOrValue).join(",")
      }
      return v;
    } else {
      return ""
    }
  },
  //分割后端传递的^号连接的字符串
  'splitArrow': function(v, char) {
    return v.replace(r_allow, char);
  },
  // 处理图片
  'multipleFiles':  function(v, textOrValue) {
    if (v) {
      if (dsf.type(v) == "string") {
        return _.map(JSON.parse(v), textOrValue).join(",")
      } else if (dsf.type(v) == "array") {
        return _.map(v, textOrValue).join(",")
      }
      return v;
    } else {
      return ""
    }
  },
};


function install(Vue) {
  for (let k in filters) {
    Vue.filter(k, filters[k]);
  }
}

export default install;

export {
  install,
  filters
}