export default {
  //数据源接口
  dbSourceInterface(ns, params) {
    let path = ns;
    if (path.startsWith("/")) {
      path = path.substr(1);
    }
    let url = "/dbsource/interface/" + path;
    return this.$http.get(url, params);
  },
  //批量查询数据字典
  //params:{'dicts':[{key:"任意的唯一标识",'dictId':数据字典ID},...]}
  getBatchDict(params) {
    params.dicts = JSON.stringify(params.dicts)
    return this.$http.get("/dict/data", params);
  },
  //保存布局
  pageBuild(params) {
    return this.$http.post("/meta/build", params);
  },
  //保存模板
  pageSaveTemplate(params) {
    return this.$http.post("/meta/saveTemp", params)
  },
  //表单数据保存
  formSave(params) {
    return this.$http.post("/meta/persistData", params, null);
  },
  getDesignerButtons(params) {
    return this.$http.get("/meta/temp/button", params, null)
      .done((data) => {
        return data;
      })
  },
  //获取可用组件组
  getComponents(params) {
    let ids = params.join(",");
    return this.$http.get("/rm/control/list", { rmIds: ids }, null)
      .then(({ data }) => {
        return data
      })
  },
  //获取指定组中的业务组件集合
  getBusinessComponets(params) {
    let ids = params.join(",");
    return this.$http.get("/meta/bc/list", { ids: ids }, null)
      .then(({ data }) => {
        return data
      })
  },

  //获取元数据树结构
  getMetaDataFieldsTree(params, hasValueAttributes) {
    let tree = [];
    function fn(data) {
      if (data.metadata) {
        let list = _.map(data.metadata, (m, i) => {
          let vas = m.valueAttributes;
          if (hasValueAttributes !== false) {
            if (vas.length > 1) {
              m.children = vas;
              _.each(vas, (v) => {
                v.fieldCaption = m.name + "(" + v.name + ")"
                v.field = m.code + "_" + v.code; //m.at.replace(/\./g, "_") + "." + m.code + "_" + v.code;
                v.id = m.at.replace(/\./g, "_") + "." + m.code + "_" + v.code;
              })
            } else {
              m.fieldCaption = m.name;
              m.field = m.code; // m.at.replace(/\./g, "_") + "." + m.code;
            }
          } else {
            m.fieldCaption = m.name;
            m.field = m.code;
          }
          return m;
        });
        list = list.sort((v1, v2) => {
          return v1.order < v2.order ? -1 : 1;
        });
        data.children = list;
        delete data.metadata;
      }
    }
    return this.$http.get('/rm/list/meta', params)
      .then(({ data }) => {
        let metadatas = data.data;
        for (let i = 0; i < metadatas.length; i++) {
          let data = dsf.mix({}, metadatas[i]);
          fn(data);
          tree.push(data);
        }
        if (params.namespace) {
          tree = _.filter(tree, (v) => {
            return v.ID == params.namespace
          })
        }
        return tree;
      });
  },
   //获取元数据树结构
   getDbSourceFieldsTree(params, hasValueAttributes) {
    let tree = [];
    function fn(data) {
      if (data.metadata) {
        let list = _.map(data.metadata, (m, i) => {
          let vas = m.valueAttributes;
          if (hasValueAttributes !== false) {
            if (vas.length > 1) {
              m.children = vas;
              _.each(vas, (v) => {
                v.fieldCaption = m.name + "(" + v.name + ")"
                v.field = m.code + "_" + v.code; //m.at.replace(/\./g, "_") + "." + m.code + "_" + v.code;
                v.id = m.at.replace(/\./g, "_") + "." + m.code + "_" + v.code;
              })
            } else {
              m.fieldCaption = m.name;
              m.field = m.code; // m.at.replace(/\./g, "_") + "." + m.code;
            }
          } else {
            m.fieldCaption = m.name;
            m.field = m.code;
          }
          return m;
        });
        list = list.sort((v1, v2) => {
          return v1.order < v2.order ? -1 : 1;
        });
        data.children = list;
        delete data.metadata;
      }
    }
    return this.$http.get('/dbsource/rmId', params)
      .then(({ data }) => {
        let metadatas = data.data;
        for (let i = 0; i < metadatas.length; i++) {
          let data = dsf.mix({}, metadatas[i]);
          fn(data);
          tree.push(data);
        }
        if (params.namespace) {
          tree = _.filter(tree, (v) => {
            return v.ID == params.namespace
          })
        }
        return tree;
      });
  },
  //获取系统角色树
  getOuaRulesTree(params) {
    return this.$http.get("/oua/getChooseTreeRole", {})
      .done((res) => {
        return res;
      })
  },
  //获取当前用户所有角色
  getCurrentUserRoles(params) {
    return this.$http.get("/oua2/current/user/role", {})
      .done((res) => {
        return res;
      })
  },
  //登录
  login(params) {
    return this.$http.post('login/login', params).then(({ data }) => {
      return data
    })
  },
  //登出
  loginOut() {
    return this.$http.post('login/loginOut').then(({ data }) => {
      return data
    })
  }

}