/* eslint-disable no-unused-vars */
import dynamicComponent from '_platform/dsf/output/dynamicComponent'

let designPages = {};

function initRouter(router, clientString) {
  //检查动态组件（通过设计器配置的页面），并动态创建路由
  const VueRouterPush = router.push;
  router.push = function push(to) {
    // if (window.$$mobile) {
    //   appendTime(to);
    // }
    return VueRouterPush.call(this, to)
      .catch(err => {
        dsf.warn(err);
      });
  };
  const VueRouterReplace = router.replace;
  router.replace = function replace(to) {
    // if (window.$$mobile) {
    //   appendTime(to);
    // }
    return VueRouterReplace.call(this, to)
      .catch(err => {
        dsf.warn(err);
      });
  };

  function appendTime(route) {
    if (window.$$mobile) {
      if (!route.query) {
        route.query = {};
      }
      if (route.query._t == undefined) {
        route.query._t = new Date().getTime().toString();
      }
    }
  }

  if (clientString) {
    router.beforeEach((to, from, next) => {
      let matched = to.matched;
      //当没有匹配到路由或者匹配到*路由时
      let isMatched = matched.length <= 0 || matched[0].path == "*";
      let clientPart = "/" + clientString + "/";
      if (isMatched && to.path.startsWith(clientPart) && !designPages[to.path]) {
        /**
         * 动态路由
         */
        let rmCode = to.path.substr(clientPart.length).replace(/\//g, '.');
        let rmCodeArr = rmCode.split(".");
        let nameSpace = rmCodeArr.slice(0, rmCodeArr.length - 1).join(".");
        let pageName = rmCodeArr[rmCodeArr.length - 1];
        dynamicComponent(rmCode, clientString, true).then((result) => {
          router.addRoutes([{
            path: to.path,
            name: result.name,
            component: result,
            meta: {
              needLogin: result.needLogin,
              title: dsf.express.replace(result.pageTitle, dsf.config),
              keepAlive: result.isKeepAlive,
              level: result.pageLevel || 1
            },
            props(router) {
              return {
                "nameSpace": nameSpace,
                "pageName": pageName,
                "queryString": router.query
              }
            }
          }]);
          designPages[to.path] = true;
          next({ path: to.fullPath });
        }).catch((err) => {
          next();
        });
      } else {
        next();
      }
    });
  }

  router.onError((error) => {
    dsf.layer.message("动态路由发生错误", false)
  })
  //将路由参数外放到window全局对象，使dsf.express表达式获取url参数功能可用
  router.afterEach((to, from) => {
    window.__dsfUrlInfo__ = to;
  });
}

function hotLoad(paths, router, clientString) {
  let routers = {};
  _.each(paths, (el) => {
    routers[el] = false;
  });
  return new Promise((resolve, reject) => {
    if (paths.length <= 0) {
      resolve();
    }
    _.each(paths, (e, i) => {
      let clientPart = "/" + clientString + "/";
      let path = e;
      let rmCode = path.substr(clientPart.length).replace(/\//g, '.');
      let rmCodeArr = rmCode.split(".");
      let nameSpace = rmCodeArr.slice(0, rmCodeArr.length - 1).join(".");
      let pageName = rmCodeArr[rmCodeArr.length - 1];
      dynamicComponent(rmCode, clientString, true)
        .then((result) => {
          router.addRoutes([{
            path: path,
            name: result.name,
            component: result,
            meta: {
              needLogin: result.needLogin,
              title: dsf.express.replace(result.pageTitle, dsf.config),
              keepAlive: result.isKeepAlive,
              level: result.pageLevel || 1
            },
            props(router) {
              return {
                "nameSpace": nameSpace,
                "pageName": pageName,
                "queryString": router.query
              }
            }
          }]);
          // console.log(path)
          designPages[path] = true;
        })
        .catch((err) => {})
        .finally(() => {
          // console.log(e);
          routers[e] = true;
          let result = _.filter(routers, (el) => !el);
          result.length <= 0 && resolve();
        })
    })
  })
}

function injectionWebAPI(Vue, api) {
  //注入WEBAPI
  Object.defineProperties(Vue.prototype, {
    $webAPI: {
      get: function() {
        if (!this.$$webapi) {
          this.$$webapi = {};
          for (let k in api) {
            this.$$webapi[k] = api[k].bind(this)
          }
        }
        return this.$$webapi;
      }
    }
  });
}

function injectionDsf(Vue, dsf) {
  Vue.prototype.dsf = dsf;
}

function injectionHttp(Vue, http) {
  Vue.prototype.$http = http;
}


export default {
  hotLoad,
  initRouter,
  injectionWebAPI,
  injectionDsf,
  injectionHttp
}