dsf.mix(true, dsf.config, {
  webRoot: {
    //默认请求路径根
    default: "/dsfa/",
    //dsfa4.0
    dsfa: '/dsfa/'
  },
  file:{
    preViewServer:"/dsfa/"
  },
  defaultTheme: "darkred-theme",
  defaultTitle: "",
  assetsDir: "./static/",
  designer: {
    gotoIcon: true,
    scssDir: "/static/",
    scss: [...dsf.config.designer.scss]
  }
}, (window.$$config ? window.$$config : {}));

window.$$webRoot = dsf.config.webRoot;