import layout from './layout';
export default {
  mixins: [layout],
  design: {
    layout: {
      exclude: ['active']
    }
  },
  props: {
    tabHeight: {
      type: String,
      default: '100%',
      desc: '组件高度'
    },
    slots: {
      type: Array,
      default: function() {
        return []
      }
    },
    tabStyle: {
      type: String,
      default: '',
      desc: '组件风格'
    },
    tabPosition: {
      type: String,
      default: 'top',
      desc: '组件位置'
    },
    enableDelete: {
      type: Boolean,
      default: false,
      desc: '标签是否可关闭'
    },
    showPadding: {
      type: Boolean,
      default: true,
      desc: '是否有边距'
    },
    openLazy: {
      type: Boolean,
      default: false,
      desc: '开启懒加载'
    },
    active: {
      type: String,
      default: '',
    }
  },
  data() {
    return {
      activeNames: [],
      activeName: this.active,
      height: '100%'
    };
  },
  created() {
    if (this.isDesign && this.slots.length == 0) {
      let v = [];
      for (let i = 0; i < 4; i++) {
        v.push({
          title: 'Tab' + (i + 1),
          name: 'tab' + (i + 1),
          disable: 'false',
          isHide: 'false',
          badge: null,
          lazy: 'false',
          controls: []
        });
      }
      this.slots = v;
    }
    this.$nextTick(() => {
      this.changeActive();
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.resizeHeight();
    });
  },
  methods: {
    isdisable(tab) {
      return this.$eval(tab.disable);
    },
    isHide(tab) {
      return !this.$eval(tab.isHide);
    },
    isLazy(tab) {
      if (this.isDesign) {
        return false;
      } else {
        return this.$eval(tab.lazy);
      }

    },
    changeActive() {
      let activeName = null;
      for (let i in this.slots) {
        let tab = this.slots[i];
        if (!activeName && !(this.$eval(tab.disable) || this.$eval(tab.isHide))) {
          activeName = tab.name;
        }
        this.activeNames.push(tab.name);
      }
      if (activeName) {
        this.activeName = activeName;
      }
    },
    resizeHeight() {
      if (this.$parent.__desContext__) {
        this.$parent.__desContext__.height = this.tabHeight;
      }
    },
    tabChange(obj) {
      // 作为二次开发切换选中tab
      // obj  this.slots[2]  也可以是 {name: this.slots[2].name}
      this.activeName = obj.name;
    },
    tabClick(tab, event) {
      // 二次开发tab选中事件监听（第一次选中和代码触发时不会调用）
      console.log(tab, event);
    },
    tabRemove(tab, event) {
      // 二次开发tab被删除事件
      // tab.index(索引)
      // tab.name(id)
      console.log(tab, event);
    },
    renderTabContent(obj) {
      // 刷新某一个内容区域
      let name = obj && obj.name ? obj.name : this.activeName;
      let slot = this.slots[this.activeNames.indexOf(name)];
      slot.isHide = 'true';
      this.$nextTick(() => {
        slot.isHide = 'false';
      });
    }
  }
}