import control from "./control";

export default {
  mixins: [control],
  props:{
    //是否是文字类组件
    isTextContent:{
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    defaultValue: {
      type: [String, Number],
      default: ''
    }
  },
  design: {
    //设计器是否在组件上产生遮罩
    isMask: false
  },
  data () {
    return {
      $value: '文本'
    }
  },
  watch: {
    'value' (to) {
      if (!to && this.defaultValue) {
        // todo Express表达式暂未实现
        // let exp = dsf.Express;
        // this.$value = exp.replace(this.defaultValue.text);
        this.$value = this.defaultValue;
      }
    }
  }
};
