<template>
  <div class="ds-component-info">
    <p><label>控件类型：</label>{{ _info.caption }}</p>
    <p><label>元数据标识：</label>{{ _info.id }}</p>
    <p><label>元数据名称：</label>{{ _info.name }}</p>
    <p><label>元数据类型：</label>{{ _info.typeText }} | {{ _info.typeValue }}</p>
  </div>
</template>

<script>
  export default {
    name: "DsfComponentInfo",
    props: {
      info: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      _info() {
        let obj = {};
        _.forOwn(this.info, (value, key) => {
          obj[key] = value || "无"
        });
        return obj
      }
    }
  }
</script>

