let noopHooks = function(data, next) {
  next();
};

let createWatcher = function($vm, key, callback, options) {
  return $vm.$watch(function() {
    try {
      return dsf.express.eval("{{" + key + "}}", $vm.$viewData);
    } catch (ex) {
      return undefined;
    }
  }, callback, options);
}

export default {
  inheritAttrs: false,
  inject: {
    $parentView: {
      default: null
    },
    $frame: {
      default: null
    },
    $dialog: {
      default: null
    },
    $isMobile: {
      default: false
    }
  },
  provide() {
    return {
      $vm: this,
      $parentView: this,
      $choiceParams: this.choiceParams
    }
  },
  props: {
    nameSpace: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    queryString: {
      type: Object,
      default () {
        return {};
      }
    },
    choiceParams: {
      type: Object,
      default () {
        return null;
      }
    }
  },
  data() {
    return {
      context: {
        $$viewInitData: {},
        $$viewData: {},
        $$enum: {}
      },
      frames: {},
      _isLoaded: false,
      isLayoutView: true,
      metadataDict: {},
      loading: false,
      flows: {},
      $forceReadOnly: false,
      $formWatchs: [],
      $choice: null,
      $isDestroyed: false,
      $isReady: false
    }
  },
  computed: {
    $page() {
      return this.$refs.page;
    },
    $viewData() {
      return this.context.$$viewData;
    },
    $viewInitData() {
      return this.context.$$viewInitData;
    },
    $enum() {
      return this.context.$$enum;
    },
    $dataKey() {
      let params = this.queryString;
      return params ? params.id : "";
    },
    $isLoaded() {
      return this.$data._isLoaded;
    },
    //页面是否被keepAlive,返回page组件的isKeepAlive属性
    $keepAlive() {
      return this.$data.$$keepAlive || false;
    }
  },
  beforeCreate() {
    this.$dialogArgs = null;
    this.$openerVm = null;
    try {
      if (window.opener) {
        if (window.opener.dialogArgs[window.name]) {
          this.$dialogArgs = JSON.parse(window.opener.dialogArgs[window.name]) || null;
        }
        if (window.opener.openerVms[window.name]) {
          this.$openerVm = window.opener.openerVms[window.name] || null;
        }
      } else if (this.$dialog) {
        this.$dialogArgs = this.$dialog.dialogArgs || null;
        this.$openerVm = this.$dialog.openerVm;
      }
    } catch (ex) {
      return null;
    }
  },
  created() {
    this.$data.$forceReadOnly = this.queryString.isview == "1";
    if (this.$frame) {
      this.$frame.content = this;
    }
  },
  updated() {
    // console.log("更新页面")
  },
  activated() {},
  deactivated() {},
  mounted() {
    if (this.isMobile) {
      window.setTimeout(() => {
        this.$init();
      }, 300)
    } else {
      this.$init();
    }
  },
  beforeDestroy() {
    // console.log("销毁")
    this.$data.$isDestroyed = true;
    this.context.$$viewData = null;
    this.context.$$viewInitData = null;
    this.$data.$choice = null;
    for (let k in this.$frames) {
      this.$delete(this.frames, k);
    }
    if (this.$frame) {
      delete this.$frame.content;
    }
    _.eachRight(this.$data.$formWatchs, (uw, i) => {
      uw();
      this.$data.$formWatchs.splice(i, 1);
    });
    this.$dialogArgs = null;
    this.$openerVm = null;
  },
  methods: {
    //初始化页面
    $init() {
      // let loading = dsf.layer.pc.loading();
      let viewData = null;
      new Promise(resolve => { resolve(); })
        .then(() => {
          if (!this.$data.$isDestroyed) {
            //获取页面初始化数据
            return this.$initData();
          }
        })
        .then(response => {
          if (!this.$data.$isDestroyed) {
            //执行初始化数据完成的钩子函数
            let inited = this.$options.inited || noopHooks;
            return promiseFn(inited).call(this, response ? response.data : {});
          }
        })
        .then(data => {
          if (!this.$data.$isDestroyed) {
            let _this = this;
            _this.context.$$viewInitData = data;
            initEnum.call(this);
            viewData = initControls.call(this);
            //将页面显示出来
            this.$data._isLoaded = true;
            if (_this.$loadData) {
              //如果存在主键参数则加载表单数据
              return _this.$loadData(this.nameSpace);
            }
          }
        })
        .then(response => {
          if (!this.$data.$isDestroyed) {
            //执行查询表单数据完成的钩子
            let dataLoaded = this.$options.dataLoaded || noopHooks;
            return promiseFn(dataLoaded).call(this, response ? response.data : {});
          }
        })
        .then(data => {
          if (!this.$data.$isDestroyed) {
            let _this = this;
            dsf.log("开始为表单进行赋值了");
            if (data) {
              dsf.mix(viewData, data);
            }
            _this.context.$$viewData = viewData;
          }
        })
        .catch(err => {
          if (dsf.type(err) == 'string') {
            dsf.layer.pc.message(err, false);
          } else if (dsf.type(err) == 'object' && err.message) {
            dsf.layer.pc.message(err.message, false);
          } else if (dsf.type(err) == "error") {
            // dsf.layer.pc.message('程序出现异常', false);
            dsf.error(err);
          }
        })
        .finally(() => {
          // this.loading=false;
          this.$nextTick(() => {
            //重置页面和下级组件的宽高
            let ready = this.$options.ready;
            ready && ready.call(this);
            this.$data.$isReady = true;
            // 注册二次开发的监听器
            // 监听的表单值
            let formWatch = this.$options.formWatch || {};

            for (let key in formWatch) {
              let item = formWatch[key];
              let fn, options = {};
              if (dsf.type(item) == 'function') {
                fn = item.bind(this);
              } else if (dsf.type(item) == 'object') {
                if (!item.handler || dsf.type(item.handler) !== 'function') {
                  break;
                }
                fn = item.handler.bind(this);
                if (item.deep) {
                  options.deep = true;
                }
                if (item.immediate) {
                  options.immediate = true;
                }
              } else {
                break;
              }
              let unwatch = createWatcher(this, key, fn, options);
              this.$data.$formWatchs.push(unwatch);
            }
          });
        });
    },
    //调用初始化接口
    $initData() {
      return new Promise((resolve, reject) => {
        let params = dsf.mix({
            pageName: this.pageName,
            namespace: this.nameSpace
          },
          this.queryString
        );
        this.$http.get("/meta/init", params)
          .done(data => {
            resolve(data);
          })
          .error(err => {
            reject({ message: err.message });
          });
      });
    },
    $getDefaultValue(key) {
      if (dsf.isDef(this.$viewInitData[key]) && dsf.isDef(this.$viewInitData[key].defaultValue)) {
        let v = this.$viewInitData[key].defaultValue;
        let type = dsf.type(v)
        if (type == 'object' || type == 'array') {
          v = dsf.mix(true, type == 'object' ? {} : [], v);
        }
        return v;
      }
    },
    $proxyFn() {
      let args = Array.prototype.slice.call(arguments, 1);
      let fnName = arguments[0];
      this[fnName] && this[fnName].apply(this, args);
    },
    $dataFormat(k, params, v) {
      if (params) {
        let filter = Vue.filter(k);
        if (filter) {
          return filter(v, params);
        }
      }
      return v;
    },
    $clone(obj) {
      return JSON.parse(JSON.stringify(obj))
    },
    reset() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k].reset) {
          this.$refs[k].reset();
        }
      }
    },
    reloadData() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k].reloadData) {
          this.$refs[k].reloadData();
        }
      }
    },
    //返回$choice中组件的选中对象
    yes() {
      let result = this.$data.$choice.getReturnValue();
      return result;
    },
    //获取所在作用域的元数据字典
    getScopeMetaDataDict(component) {
      if (dsf.isEmptyObject(this.metadataDict)) {
        return {};
      }
      let metadata = component.metadata;
      if (!metadata || !component.metadata_fullcode) {
        return {};
      }
      let fullCode = component.metadata_fullcode;
      if (this.metadataDict[fullCode]) {
        if (this.metadataDict[fullCode].inSubTable) {
          let result = {};
          _.each(this.metadataDict, (el, i) => {
            if (el.inSubTable === this.metadataDict[fullCode].inSubTable) {
              result[i] = el;
            }
          });
          return result;
        } else {
          let result = {}
          _.each(this.metadataDict, (el, i) => {
            if (dsf.isUnDef(el.inSubTable)) {
              result[i] = el;
            }
          });
          return result;
        }
      }
    }
  },
  watch: {
    $isLoaded: {
      handler(nv, ov) {
        if (nv != ov && nv === true) {
          this.$nextTick(() => {
            //调用布局resize
            dsf.resizeComponent(this.$page);
            this.$emit("inited", this);
          });
        }
      }
    }
  }
};

//将方法转换为promise方法
function promiseFn(fn) {
  return function() {
    let _array = Array.prototype.slice.call(arguments);
    let self = this;
    return new Promise((resolve, reject) => {
      let next = mark => {
        if (mark === false) {
          reject.apply(null, _array);
        } else {
          resolve.apply(null, _array);
        }
      };
      fn.apply(self, _array.concat([next]));
    });
  };
}

function initEnum() {
  for (let k in this.context.$$viewInitData) {
    let d = this.context.$$viewInitData[k];
    if (d && d.dataSource) {
      this.$set(this.context.$$enum, k, d.dataSource);
    }
  }
}

function initControls() {
  let vd = {};
  let controls = this.$controls;
  let initData = this.$viewInitData;
  for (let k in controls) {
    let ctrl = controls[k];
    //如果isview=1页面只读
    if (ctrl.isFormControl && this.$data.$forceReadOnly) {
      ctrl.readOnly = true;
    }
    if (ctrl.metadata) {
      let key = ctrl.metadata_fullcode;
      if (ctrl.metadata.level == 0) {
        if (initData[key]) {
          //将默认值赋予viewData
          if (dsf.isDef(this.$getDefaultValue(key))) {
            vd[ctrl.metadata_fullcode] = this.$getDefaultValue(key);
          }
        }
      } else if (ctrl.metadata.level == 1 && ctrl.isOneToMany) {
        if (dsf.isDef(this.$getDefaultValue(key))) {
          vd[ctrl.metadata_fullcode] = this.$getDefaultValue(key);
        }
      }
    }
  }
  return vd;
}