<template>
  <div v-show="loaded" class="full-view">
    <!-- <div ref="pageloader"></div> -->
    <component :is="'PreviewLayout'" v-if="loaded" class="page-head-font-fixed" />
  </div>
</template>
<script>
// import func from '../../../vue-temp/vue-editor-bridge';
// import template from './template';
// import sysButton from '_platform/dsf/pc/sysButton';

export default {
  name: "DsfPreView",
  props: {
    mobile: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      layout: {},
      loaded: false,
      templateStr: "",
      domTree: null
    };
  },
  created() { },
  mounted() {
    this.loadLayout();
  },
  destroyed() {
    if (this.domTree) {
      this.domTree.$destroy();
    }
  },
  methods: {
    loadLayout() {
      let context = dsf.storage.session.get("layoutContent");// window.localStorage.getItem("layoutContent");
      let dom = $("<context>" + context + "</context>");
      let scriptNode = dom.children("script[id='layout']");
      let code = ["var layout=" + $.trim(scriptNode.html()) + ";", "return layout"];
      let getData = new Function(code.join("\n"));
      let layoutData = dsf.fillComponentDefaultProps(getData());
      layoutData["module-name"] = layoutData.context.B + "." + layoutData.context.M + "." + layoutData.name;
      let result = dsf.designer.convertTemplateAndControlData(layoutData, true);
      let tpl = result.tpl;
      let pageRender = Vue.compile(tpl);
      let options = getViewMixin.call(this, layoutData, result.props, pageRender.render);
      // let DomTree = Vue.extend(options);
      // this.domTree = new DomTree().$mount(this.$refs.pageloader);
      // Vue.component('PreviewLayout',options)
      this.$options.components['PreviewLayout'] = options
      this.loaded = true;
      this.$nextTick(() => {
        //重置页面尺寸
        dsf.resizeComponent();
      })
    }
  }
};

function getViewMixin(layout, props, renderFn) {
  let base = {
    render: renderFn,
    name: "PreviewLayout",
    inject: {
      $parentView: {
        default: null
      },
      $frame: {
        default: null
      }
    },
    provide() {
      return {
        $vm: this,
        $parentView: this
      }
    },
    mixins: [],
    data() {
      return {
        context: {
          $$viewInitData: {},
          $$viewData: {},
          $$enum: {}
        },
        $$controls: props,
        isPreView: true,
        frames: {},
        _isLoaded: false,
        loading: false,
        metadataDict: {},
        flows: {}
      };
    },
    props: {
      queryString: {
        type: Object,
        default() {
          return {};
        }
      },
      choiceParams: {
        type: Object,
        default() {
          return null;
        }
      }
    },
    computed: {
      $page() {
        return this.$refs.page;
      },
      $viewData() {
        return this.context.$$viewData || {};
      },
      $viewInitData() {
        return this.context.$$viewInitData;
      },
      $enum() {
        return this.context.$$enum;
      },
      $dataKey() {
        let params = this.queryString;
        return params ? params.id : "";
      },
      $controls() {
        return this._data.$$controls;
      },
      $isLoaded() {
        return this.$data._isLoaded;
      },
      $keepAlive() {
        return false
      }
    },
    created() {
      if (this.$parentView && this.$frame) {
        let name = this.$frame.caption || this.$frame.$attrs.id;
        if (name) {
          this.$parentView.frames[this.$frame.caption].content = this;
        }
      }
    },
    mounted() {
      this.$data._isLoaded = true;
      this.$nextTick(() => {
        window.preViewRef = this.$refs;
        window.__preView = this;
      })

    },
    beforeDestroy() {
    },
    methods: {
      $clone(obj) {
        return JSON.parse(JSON.stringify(obj));
      },
      $proxyFn() {
        let args = Array.prototype.slice.call(arguments, 1);
        let fnName = arguments[0];
        this[fnName] && this[fnName].apply(this, args);
      },
      $getDefaultValue() {
      },
      $dataFormat(k, params, v) {
        if (params) {
          let filter = Vue.filter(k);
          if (filter) {
            return filter(v, params);
          }
        }
        return v;
      },
      updateControlPropsInTemplate(designId, scope) {
        let key = designId + "-" + scope.row.$hash;
        let props = null;
        if (!this.$controls[key]) {
          props = this.$clone(this.$controls[designId])
          this.$set(this.$controls, key, props);
        } else {
          props = this.$controls[key];
        }
        if (scope._self.updateControlPropsInTemplate) {
          props = scope._self.updateControlPropsInTemplate(props, scope);
        }
        if (this.$updateControlPropsInTemplate) {
          props = this.$updateControlPropsInTemplate(props, scope);
        }
        return props;
      },
      $updateControlPropsInTemplate(ctrlProps) {
        return ctrlProps;
      }
    }
  };
  if (layout.pageType == 'form' || layout.pageType == 'question') {
    base.mixins.push(window.$$form)
  }
  return base;
}
</script>
